@-webkit-keyframes toLeft {
  0% {
    -webkit-transform: skewX(0deg);
            transform: skewX(0deg);
  }
  40% {
    -webkit-transform: skewX(-20deg);
            transform: skewX(-20deg);
  }
  100% {
    -webkit-transform: skewX(0deg);
            transform: skewX(0deg);
  }
}@keyframes toLeft {
  0% {
    -webkit-transform: skewX(0deg);
            transform: skewX(0deg);
  }
  40% {
    -webkit-transform: skewX(-20deg);
            transform: skewX(-20deg);
  }
  100% {
    -webkit-transform: skewX(0deg);
            transform: skewX(0deg);
  }
}
@-webkit-keyframes toRight {
  0% {
    -webkit-transform: skewX(0deg);
            transform: skewX(0deg);
  }
  40% {
    -webkit-transform: skewX(20deg);
            transform: skewX(20deg);
  }
  100% {
    -webkit-transform: skewX(0deg);
            transform: skewX(0deg);
  }
}
@keyframes toRight {
  0% {
    -webkit-transform: skewX(0deg);
            transform: skewX(0deg);
  }
  40% {
    -webkit-transform: skewX(20deg);
            transform: skewX(20deg);
  }
  100% {
    -webkit-transform: skewX(0deg);
            transform: skewX(0deg);
  }
}
@-webkit-keyframes textShow {
  0% {
    opacity: 0.1;
    -webkit-transform: translateX(-20px);
            transform: translateX(-20px);
  }
  100% {
    opacity: 1;
    -webkit-transform: translateX(0px);
            transform: translateX(0px);
  }
}
@keyframes textShow {
  0% {
    opacity: 0.1;
    -webkit-transform: translateX(-20px);
            transform: translateX(-20px);
  }
  100% {
    opacity: 1;
    -webkit-transform: translateX(0px);
            transform: translateX(0px);
  }
}
.container {
  width: 800px;
  height: 500px;
}
.container .pagesContainer {
  display: flex;
  justify-content: flex-start;
  overflow: auto;
  height: 100%;
  scroll-behavior: smooth;
}
.container .pagesContainer .page {
  flex-shrink: 0;
  height: 100%;
  border: 2px solid green;
  box-sizing: border-box;
  width: 100%;
  background-color: pink;
  transition: -webkit-transform 0.4s linear;
  transition: transform 0.4s linear;
  transition: transform 0.4s linear, -webkit-transform 0.4s linear;
  -webkit-transform: skewX(0);
          transform: skewX(0);
  text-align: center;
}
.container .pagesContainer .visiblePage h2,
.container .pagesContainer .visiblePage h3 {
  -webkit-animation: textShow 1s ease-in;
          animation: textShow 1s ease-in;
}
.container .pagesContainer .currentLeft {
  -webkit-transform-origin: left top;
          transform-origin: left top;
  position: relative;
  width: 800px;
  z-index: 10;
  -webkit-animation: toLeft 0.5s linear;
          animation: toLeft 0.5s linear;
}
.container .pagesContainer .currentRight {
  -webkit-transform-origin: right top;
          transform-origin: right top;
  position: relative;
  width: 800px;
  z-index: 10;
  -webkit-animation: toRight 0.5s linear;
          animation: toRight 0.5s linear;
}
